/* Ховер на десктопах */
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.ttf") format("truetype");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Regular.ttf") format("truetype");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/RobotoCondensed-Bold.ttf") format("truetype");
  font-weight: 700;
  font-display: swap;
}
@font-face {
  font-family: "SFSquareHead";
  src: url("../fonts/SFSquareHead.ttf") format("truetype");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "SourceSansVariable";
  src: url("../fonts/SourceSansVariable-Roman.otf") format("opentype");
  font-weight: 400;
  font-display: swap;
}
@keyframes bounce {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(5px);
  }
  100% {
    transform: translateY(0);
  }
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  font-family: "Montserrat", sans-serif;
  background-color: #ffffff;
  color: #ffffff;
  /* iOS fix */
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}

main {
  overflow: hidden;
  position: relative;
  z-index: 1;
}

a {
  color: #000000;
  text-decoration: none;
}

ul,
ol,
nav {
  list-style: none;
  padding: 0;
}

svg {
  width: auto;
  height: auto;
}

img {
  display: block;
  max-width: 100%;
}

input,
button {
  -webkit-appearance: none;
}

input,
button,
select,
textarea {
  font: inherit;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0;
}

input,
textarea {
  padding: 0;
  margin: 0;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0;
}

/* Blocks */
.graphic {
  width: 100%;
  height: 100%;
  background-color: #403f4d;
  padding: 190px 98px 40px 143px;
}
.graphic__wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 50px;
  width: 100%;
}
.graphic__left {
  width: calc(33% - 25px);
}
.graphic__right {
  width: calc(67% - 25px);
}

.canvas {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  border-radius: 70px;
  border: 1px solid #ffffff;
  padding-bottom: 50px;
}
.canvas::before {
  content: "";
  position: absolute;
  top: 148px;
  left: 100%;
  width: 50px;
  height: 1px;
  background-color: #ffffff;
}
.canvas__window {
  width: 100%;
  overflow: hidden;
}
.canvas__window canvas {
  overflow: hidden;
}
.canvas__image {
  max-width: 400px;
  width: 100%;
  margin-top: 30px;
}
.canvas__image svg {
  width: 100%;
  height: 330px;
}

.desc__title {
  position: relative;
  display: flex;
  align-items: center;
  gap: 43px;
  border: 1px solid #7cbb3b;
  border-radius: 70px;
  padding: 53px 300px 63px 40px;
}
.desc__title::before, .desc__title::after {
  content: "";
  position: absolute;
  left: 89px;
}
.desc__title::before {
  top: 100%;
  width: 60%;
  height: 274px;
  border-left: 1px solid #7cbb3b;
  border-bottom: 1px solid #7cbb3b;
}
.desc__title::after {
  top: calc(100% + 273px);
  width: 923px;
  height: 312px;
  border: 1px solid #7cbb3b;
}
.desc__title svg {
  position: absolute;
  top: 28px;
  right: 21px;
}
.desc__title-num {
  font-family: "SFSquareHead", sans-serif;
  font-size: 140px;
  letter-spacing: -0.05em;
}
.desc__title-num span {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
}
.desc__title-text {
  font-family: "OpenSans", sans-serif;
  font-size: 75px;
  line-height: 1;
}
.desc__main {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px 68px;
  max-width: 955px;
  width: 100%;
  padding-left: 193px;
  padding-top: 128px;
}
.desc__item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 31px;
  width: calc(50% - 34px);
  border: 1px solid #7cbb3b;
  background-color: #403f4d;
  border-radius: 50px;
  padding: 38px 10px 26px;
}
.desc__item::before, .desc__item::after {
  content: "";
  position: absolute;
  top: 130px;
  z-index: 1;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  border: 1px solid #7cbb3b;
  background-color: #403f4d;
}
.desc__item::before {
  left: -12px;
}
.desc__item::after {
  right: -12px;
}
.desc__text {
  font-size: 21px;
  text-align: center;
}
.desc--3 .desc__item:nth-child(2)::after {
  content: "";
  position: absolute;
  top: 143px;
  left: -35px;
  z-index: 0;
  width: 1px;
  height: 100%;
  border-radius: unset;
  border: unset;
  background-color: #7cbb3b;
}
.desc--3 .desc__item:nth-child(3)::before {
  top: -12px;
  left: 50%;
  transform: translateX(-50%);
}
.desc--3 .desc__item:nth-child(3)::after {
  display: none;
}
.desc--4 .desc__item:nth-child(2)::after, .desc--4 .desc__item:nth-child(4)::after {
  right: -70px;
}